nt:infinite;animation-iteration-count:infinite}@-webkit-keyframes wiggle-u68e01032{from{transform:rotateZ(10deg)}to{transform:rotateZ(-10deg)}}@keyframes wiggle-u68e01032{from{transform:rotateZ(10deg)}to{transform:rotateZ(-10deg)}}#tech-icon-5{-webkit-animation-name:wiggle-u8e83de87;animation-name:wiggle-u8e83de87;-webkit-animation-duration:.8s;animation-duration:.8s;-webkit-animation-delay:.3s;animation-delay:.3s;-webkit-animation-direction:alternate;animation-direction:alternate;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}@-webkit-keyframes wiggle-u8e83de87{from{transform:rotateZ(10deg)}to{transform:rotateZ(-10deg)}}@keyframes wiggle-u8e83de87{from{transform:rotateZ(10deg)}to{transform:rotateZ(-10deg)}}

.tech{margin:0 2rem;display:grid;grid-template-columns:repeat(auto-fit, minmax(120px, 1fr))}

.project__card{padding:1rem 0}.project__img{display:flex}.project__img img{height:170px}.project__article h3{font-size:2rem}@media (min-width: 768px){.project__img img{height:450px}}@media (min-width: 920px){.project__card{display:flex;flex-direction:row;justify-content:center}.project__img{max-width:30vw}.project__img img{width:100%;height:auto}.project__article{max-width:50vw;padding:0 2rem;display:flex;flex-direction:column}}

.project{color:#fff;background:#ba094e;padding:2rem;margin:0}

.form{display:flex;justify-content:center}.form__container{display:flex;flex-direction:column;align-content:center;width:60%}.form__container label,.form__container input,.form__container textarea{width:100%;margin:.5rem 0;padding:.5rem;outline:none;border-radius:5px}.form__container textarea{height:30vh}.form__container label{font-weight:bold}

.footer{display:flex;justify-content:space-around;align-items:center;margin:1rem}.footer__logo{display:flex}.footer__logo a{text-decoration:none;padding-left:1rem}.footer__logo i{color:#000;font-size:2.5rem}.footer__text{padding:0 1rem;font-weight:bold;align-items:center;display:flex;flex-direction:column}@media (min-width: 768px){.footer{display:flex;justify-content:flex-end;align-items:center}.footer__text{flex-direction:row}.footer__text div{padding-right:5px}}

